
import React from 'react';

class control extends React.Component { 
  onClick=(sortBy, sortValue)=>{
    this.props.onSort(sortBy,sortValue)
  }
  render(){
      return (
        <div className="dropdown">
          <button className="btn btn-default dropdown-toggle" type="button" data-toggle="dropdown">Sap Xep
          <span className="caret"></span></button>
          <ul className="dropdown-menu">
            <li onClick={()=>this.onClick('name', 1)}><a href="#">Ten A-Z</a></li>
            <li onClick={()=>this.onClick('name', -1)}><a href="#">Ten Z-A</a></li>
            <li className="divider"></li>
            <li onClick={()=>this.onClick('status', 1)}><a href="#">Trang thai kich hoat</a></li>
            <li onClick={()=>this.onClick('status', -1)}><a href="#">Trang thai an</a></li>
          </ul>
        </div>
      )
    }
}

export default control;
