
import React from 'react';

class taskForm extends React.Component { 
  constructor(props){
    super(props);
    this.state={
      id:'',
      name: '',
      status: false
    }
  }
  onToggleform = () => {
    this.props.isDisplayForm()
  }
  onCloseForm = () => {
    this.props.closeFrom()
  }
  onChange = (event)=>{
    var target = event.target;
    var name = target.name;
    var value = target.value;
    if (name === 'status') {
      value = target.value === "true" ? true : false;
    }
    this.setState({
      [name]: value
    })
  }
  onSubmit =(event)=>{
    event.preventDefault();
    this.props.onSubmit(this.state);
    this.onClear();
    this.onCloseForm();
  }
  onClear = () =>{
    this.setState({
      id: '',
      name: '',
      status: false
    })
  }
  componentWillMount(){
    if (this.props.task) {
      this.setState({
        id: this.props.task.id,
        name: this.props.task.name,
        status: this.props.task.status
      })
    }
  }
  componentWillReceiveProps(nextprops){
    if (nextprops && nextprops.task) {
      this.setState({
        id: nextprops.task.id,
        name: nextprops.task.name,
        status: nextprops.task.status
      })
    } else if (!nextprops.task) {
      this.setState({
      id: '',
      name: '',
      status: false
      })
    }
  }
  render(){
    var {id} = this.state;
      return (
        <div>
            <div className="panel panel-danger">
                  <div className="panel-heading">
                    <h3 className="panel-title">{id !== ''? "Sua cong viec" : "Them Cong Viec"}
                      <button type="button" className="btn btn-warning" onClick={this.onCloseForm}>hủy</button>
                    </h3>
                  </div>
            </div>
            <form className="form-horizontal" onSubmit={this.onSubmit}>
                <div className="form-group">
                  <label>Ten Cong Viec:</label>
                  <input 
                    type="text" 
                    name="name" 
                    className="form-control" 
                    value={this.state.name}
                    onChange={this.onChange}
                  />
                </div>
                <div className="form-group">
                  <label>Trang Thai</label>
                  <select 
                    name="status" 
                    id="input" 
                    className="form-control" 
                    required="required"
                    onChange={this.onChange}
                    value={this.state.status}>
                    <option value={true}>Kich hoat</option>
                    <option value={false}>An</option>
                  </select>
                </div>
                <div className="form-group">
                  <div className="col-sm-10 col-sm-offset-2">
                    <button type="submit" className="btn btn-success">Luu Lai</button>&nbsp;
                    <button type="submit" className="btn btn-warning" onClick={this.onClear}>Huy Bo</button>
                  </div>
                </div>
            </form>
        </div>
      );
    }
}

export default taskForm;
